.advantages {
  background-color: @grey;
}

.advantages__wrapper {
  margin: 0 auto;
  width: 280px;

  @media (min-width: @tablet-width) {
    width: 620px;
  }

  @media (min-width: @desktop-width) {
    width: 960px;
  }
}

.advantages__slogans {
  background-color: @white;
  padding: 41px 20px 32px;

  @media (min-width: @tablet-width) {
    padding: 49px 20px 48px;
  }

  @media (min-width: @desktop-width) {
    padding: 67px 10px 76px;
  }
}

.advantages__slogan {
  font-size: 24px;
  line-height: 28px;
  font-weight: 300;
  margin: 0 auto;
  margin-bottom: 31px;
  text-align: center;

  @media (min-width: @tablet-width) {
    margin-bottom: 0;
    font-size: 40px;
    line-height: 54.47px;
  }

  @media (min-width: @desktop-width) {
    font-size: 50px;
    line-height: 68.09px;
  }
}

.advantages__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 53px 20px 282px;
  list-style: none;
  margin: 0;

  @media (min-width: @tablet-width) {
    flex-direction: column;
    align-items: flex-end;
    padding: 103px 20px 2px;
  }

  @media (min-width: @desktop-width) {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-column-gap: 68px;
    padding: 366px 154px 59px;
  }
}

.advantages__item {
  width: 280px;
  text-align: center;
  margin-bottom: 43px;

  @media (min-width: @tablet-width) {
    margin-bottom: 46px;
    width: 310px;
    text-align: left;
  }

  @media (min-width: @desktop-width) {
    width: 240px;
    line-height: 30px;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: start;
  }
}

.advantages__title {
  color: @black;
  font-size: 18px;
  line-height: 24px;
  margin: 0;

  @media (min-width: @desktop-width) {
    margin-bottom: 24px;
    font-size: 30px;
  }
}

.advantages__description {
  margin: 0;

  @media (min-width: @tablet-width) {
    margin-top: 12px;
    margin-bottom: 16px;
  }

  @media (min-width: @desktop-width) {
    font-size: 18px;
    line-height: 30px;
  }
}
